<template>
  <h4>Today's tasks</h4>

  <div class="container">

    <div class="wrapper-stat">
      <div class="stat">
        <h5>Business</h5>
        <div class="wrapper-progress">
          <div class="bar" :style="{width: `${statistics.business}`}"></div>
        </div>
      </div>

      <div class="stat">
        <h5>Personal</h5>
        <div class="wrapper-progress">
          <div class="bar personal" :style="{width: `${statistics.personal}`}"></div>
        </div>
      </div>
    </div>


    <div class="tasks">
      <div
          v-for="task in tasks"
          :key="task._id"
          class="task"
          :class="{ completed: task.isCompleted }"
          @click="completedHandler(task._id)"
      >
        <div class="round"
             :class="{
        business: task.type === 'business',
        personal: task.type === 'personal'
        }"
        ></div>

        <span>{{ task.name }}</span>

        <div class="remove-task-icon" @click="removeTask(task._id)">
          <img :src="require('@/assets/delete.svg')" alt="">
        </div>
      </div>
    </div>

    <div class="add-task">
      <input
          type="text"
          placeholder="Enter your task..."
          v-model="taskName"
          v-on:keyup.enter="addTask"
      >
      <button @click="addTask">+</button>
    </div>
  </div>

</template>

<script>

export default {
  name: 'App',
  data() {
    return {
      taskName: '',
      tasks: [
        {
          _id: 'iwqdoiq1234',
          name: 'Create to do app in Vue js',
          isCompleted: false,
          type: 'business'
        },
        {
          _id: 'fsdfdga',
          name: 'Go to the gym',
          isCompleted: false,
          type: 'personal'
        }
      ],
      // statistics: {}
    }
  },
  methods: {
    completedHandler(taskId) {
      const currentTask = this.tasks.find((t) => t._id === taskId)
      currentTask.isCompleted = !currentTask.isCompleted
      this.saveTasks()
    },

    addTask () {
      // if (this.taskName.trim() !== '') {
        this.tasks.push({
          _id: Math.random().toString(36).substring(2, 7),
          name: this.taskName,
          isCompleted: false,
          type: 'personal'
        })
      // }

      this.taskName = ''
      this.saveTasks()
    },

    removeTask: function (taskId) {
      this.tasks = this.tasks.filter((t) => t._id !== taskId)
      this.saveTasks()
    },

    saveTasks() {
      const parsedTasks = JSON.stringify(this.tasks);
      localStorage.setItem('tasks', parsedTasks);
    }
  },
  computed: {
    statistics: function () {
      const count = this.tasks.length || 1
      const businessData = this.tasks.filter(t => t.type === 'business')
      const personalData = this.tasks.filter(t => t.type === 'personal')

      return {
        business: Math.round((businessData.length * 100) / count) + '%',
        personal: Math.round((personalData.length * 100) / count) + '%'
      }
    }
  },
  mounted() {
    if (localStorage.getItem('tasks')) {
      try {
        this.tasks = JSON.parse(localStorage.getItem('tasks'));
      } catch(e) {
        localStorage.removeItem('tasks');
      }
    }
  }
}
</script>

